<template>
	<v-container fluid>
		<itemList />
		<v-footer
			app
			color="primary"
			height="54"
		>
			<v-col
				cols="12"
				class="text-center font-weight-bold white--text"
			>
				Total: {{ currencyFormat(total) }}
				<item-creator />
			</v-col>
		</v-footer>
	</v-container>
</template>

<script>
	import itemList from '@/components/item-list.vue'
	import itemCreator from '@/components/item-creator'
	import helpers from '@/mixins/helpers'
	export default {
		components: { itemList, itemCreator },
		mixins: [ helpers ],
		computed: {
			total() {
				let result = this.$store.state.bill.items.reduce( (sum, { subtotal }) => sum+subtotal, 0 )
				return Math.round((result+Number.EPSILON)*100)/100
			}
		}
	}
</script>